<template>
  <view>
    <view class="content">
      <form @submit="submitForm">
        <view class="input-group">
          <input type="text" placeholder="请输入文字" v-model="inputValue" />
        </view>
        <button form-type="submit" class="submit-btn">提交</button>
      </form>
    </view>
    <view class="container">
      <view class="add-button" @click="addNew">
        <!-- 这里可以使用自定义的图标或者uniapp的内置图标 -->
        <text class="uni-icons">&#xe296;</text> 添加
      </view>
      <form @submit="confirmSubmit">
        <view class="input-wrap">
          <input type="text" placeholder="请输入人员ID" v-model="personId" />
        </view>
        <button form-type="submit" class="confirm-btn">确认</button>
      </form>
    </view>
    <view class="container">
      <view class="dropdown" @click="toggleDropdown">
        <text class="dropdown-icon">▼</text>
        请选择职位名称
      </view>
      <picker mode="selector" :range="positionNames" :value="selectedPositionIndex" @change="onPositionChange">
        <view class="picker">
          已选职位：{{ selectedPosition }}
        </view>
      </picker>
    </view>
    <member-list></member-list>
  </view>
</template>

<script>
import MemberList from "../../component/team/memberList.vue";

export default {
  components: {MemberList},
  data() {
    return {
      inputValue: '', // 用户输入的数据
      personId: '' ,// 绑定到输入框的数据
      vacantPositions: [
        {
          "positionName": "Backend Developer",
          "vacancy": 1
        },
        {
          "positionName": "UI/UX Designer",
          "vacancy": 2
        }
      ],
      selectedPositionIndex: 0, // 默认选择第一个职位
      showDropdown: false // 控制下拉菜单的显示
    };
  },
  computed: {
    positionNames() {
      return this.vacantPositions.map(item => item.positionName);
    },
    selectedPosition() {
      return this.vacantPositions[this.selectedPositionIndex].positionName;
    }
  },
  methods: {
    submitForm(e) {
      e.preventDefault();
      if (this.inputValue.trim() === '') {
        uni.showToast({
          title: '输入内容不能为空',
          icon: 'none'
        });
      } else {
        // 这里处理用户提交的数据
        console.log('用户输入的内容是:', this.inputValue);
        // 清空输入框
        this.inputValue = '';
      }
    },
    addNew() {
      // 处理添加新人员的逻辑
      console.log('添加新人员');
    },
    confirmSubmit(e) {
      e.preventDefault();
      // 处理确认提交的逻辑
      console.log('提交的人员ID:', this.personId);
    },
    toggleDropdown() {
      this.showDropdown = !this.showDropdown;
    },
    onPositionChange(event) {
      this.selectedPositionIndex = event.detail.value;
    }
  }
};
</script>

